body{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
}
@media (max-width: 576px) {
  .media-phone{
      width:100% !important;
      height:100vh;
  }
}
@media (max-width: 768px) {
  .media-phone{
      width:576px;
      height:100vh;

  }
}
@media (max-width: 992px) {
  .media-phone{
      width:576px;
      height:100vh;

  }
}
@media screen and (max-width: 1200px) {
  .media-phone{
      height:100vh;
      width:576px;
  }
}
.media-phone{
      height:100vh;
      width:576px;
  }
.media-phone .show-items-photo-video{
  height:100%;
}
.show-items-photo-video .image-fit{
  height:100%;
}
.media-phone .btn-next-back{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width:100%;
  transition: 1s;
}
#btnPlay{
  opacity: 1;
  transition: 1s;
}